<template>
  <div class="zm-table">
    <el-table 
    :data="dataInfo" 
    style="width: 100%" 
    @selection-change="handleSelectionChange" >
      <!-- 选择列 -->
      <el-table-column type="selection" width="50" align="center" />
      <!-- 序号列 -->
      <el-table-column type="index" label="序号" align="center" width="70" v-if="ispc" />
      <!-- 其他列 -->
      <template v-for="propItem in tableList" :key="propItem.prop">
        <!-- 注：每一个el-table-column相当于一列 -->
        <el-table-column
          :prop="propItem.prop"
          :label="propItem.label"
          width="proItem.minWidth"
          align="center"
          show-overflow-tooltip
        >
          <!-- 默认插槽 -->
          <template #default="scope">
            <slot :name="propItem.slotName" :row="scope.row">
              {{ scope.row[propItem.prop] }}
            </slot>
          </template>
        </el-table-column>
      </template>
      <!-- 关进黑屋 -->
      <el-table-column label="状态" align="center" width="120">
        <el-button plain type="danger">关进黑屋</el-button>
      </el-table-column>
      <!-- 直播商品 -->
      <el-table-column label="分析" align="center" width="120">
        <el-button type="primary">直播商品</el-button>
      </el-table-column>
    </el-table>

    <!-- 表格尾部（插槽） -->
    <div class="footer" v-if="ispc">
      <slot name="footer">
        <el-pagination
          v-model:currentPage="page.currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="page.pageSize"
          :total="livesCount"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </slot>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits } from "vue";
import { isPC } from "@/utils/isPc";

const props = defineProps({
  dataInfo: {
    type: Array,
    default() {
      return [];
    },
  },
  page: {
    type: Object,
    default: () => {
      return {
        currentPage: 0,
        pageSize: 10,
      };
    },
  },
  livesCount: {
    type: Number,
    default: 0,
  },
});
// 展示数据
const tableList = [
  { prop: "anchorName", label: "主播名称", minWidth: "100" },
  { prop: "liveTitle", label: "直播名称", minWidth: "120" },
  { prop: "visitNum", label: "观众数", minWidth: "110", slotName: "visitNum" },
  { prop: "totalAmount", label: "销售金额", minWidth: "100", slotName: "totalAmount"},
  { prop: "liveQuantity", label: "销售数量", minWidth: "110", slotName: "liveQuantity" },

];

// 查看哪一个行被选中
const handleSelectionChange = () => {
  console.log("哪一个选项被相中");
};
const em = defineEmits(["update:page"]);
// 展示n/条页
const handleSizeChange = (pageSize) => {
  em("update:page", { ...props.page, pageSize });
};
//跳转到某一页
const handleCurrentChange = (currentPage) => {
  em("update:page", { ...props.page, currentPage });
};

let ispc = isPC();
</script>

<style scoped>
.zm-table {
  padding: 20px;
  border-top: 20px solid #f5f5f5;
}
.footer {
  margin-top: 15px;
  text-align: right;
}
</style>
